body {
    font-family: 'Open Sans', Arial ,sans-serif;
    margin: 0 auto;
}
h3 {
    @extend %small-bold;
    color: $secondary-color;
    text-align: center;
    padding: 100px 0 30px;
}
section {
    box-sizing: border-box;
    padding: 0 15px;
}
.bg-theme {
    background: #e5eaee;
}
header {
    background: url("../images/background.jpg");
    background-position: center;
    background-size: cover;
    padding-bottom: 50px;
    nav {
        text-align: center;
        padding: 20px 0;
        a {
            display: inline-block;
            margin-left: 20px;
            color: rgba(0, 0, 0, 0.7);
            @extend %small-bold;
            text-decoration: none;
            &:hover {
                color: rgba(0, 0, 0, 1);
            }
        }
    }
    h1 {
        text-align: center;
        font-size: 72px;
        color: white;
        text-transform: uppercase;
        letter-spacing: 0.05rem;
        margin-bottom: 20px;
        padding: 100px 15px 0;
        span {
            display: inline-block;
            padding: 0.5rem 0.2rem;
            border: white solid 10px;
        }
    }
    .h1-caption {
        text-align: center;
        font-weight: 700;
        color: white;
        letter-spacing: 0.05rem;
    }
    @media (max-width:640px) {
        h1 {
            padding: 0 5px;
        }
    }
}
.about-section {
    .about-img-wrapper {
        margin: 0 auto;
        max-width: 780px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .face-img {
            height: 200px;
            width: 200px;
            border-radius: 50%;
            background: url("../images/face-1.jpg") center top;
            background-repeat: no-repeat;
            order: 3;
        }
        [class^="interests-"] {
            height: 100px;
            width: 100px;
            background: lightgray center;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            &.interests-embed {
                order: 1;
            }
            &.interests-mouse {
                order: 2;
            }
            &.interests-web {
                order: 4;
            }
            &.interests-mov {
                order: 5;
            }
        }
    }
    @media screen and (max-width: 670px) {
        .about-img-wrapper {
            display: block;
            text-align: center;
            .face-img {
                display: block;
                margin: 0 auto 30px;
            }
            [class^="interests-"] {
                display: inline-flex;
                margin: 10px;
            }
        }
    }
    .about-description {
        max-width: 620px;
        margin: 50px auto;
        font-size: 24px;
        text-align: center;
        font-weight: 300;
        font-weight: normal;
        strong {
            font-weight: 700;
        }
        a {
            text-decoration: none;
            color: $accent-color;
        }
    }
    .skill-wrapper {
        max-width: 700px;
        margin: 100px auto;
        color: #999;
    }
    .skill-holder {
        display: inline-block;
        width: 49%;
        text-align: center;
        @media (max-width:500px) {
            width: 100%;
        }
    }
    .skill-desc {
        margin-bottom: 5px;
    }
    .skill-icons {
        margin-bottom: 30px;
        span {
            background: $accent-color;
            display: inline-block;
            height: 14px;
            width: 14px;
            border-radius: 50%;
            &:after {
                content: " ";
                display: block;
                height: 10px;
                width: 10px;
                border-radius: 50%;
                background: #fff;
                margin: 2px;
            }
        }
    }
    @for $i from 1 through 10 {
        .skill-#{$i} span:nth-child(n+#{$i+1}) {
            @extend %skill-color;
        }
    }
}
.portfolio-section {
    .thumb-container {
        font-size: 0;
        max-width: 960px;
        margin: 0 auto;
        padding-bottom: 100px;
    }
    .portfolio-thumbnail {
        position: relative;
        font-size: initial;
        display: inline-block;
        width: 50%;
        padding-top: 26%;
        overflow: hidden;
        @media screen and (max-width: 650px) {
            width: 100%;
            padding-top: 40%;
        }
        &:hover .portfolio-div {
            position: absolute;
            background: fade-out($accent-color, 0.1);
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            transform: rotateX(0deg);
            opacity: 1;
        }
    }
    .bhagaskara {
        @extend %thumbnail-bg;
        background: url("../images/Bhagaskara.png");
    }
    .home-dark {
        @extend %thumbnail-bg;
        background: url("../images/home-dark.png");
    }
    .sit-on-chair {
        @extend %thumbnail-bg;
        background: url("../images/sitOnChair.png");
    }
    .mailing {
        @extend %thumbnail-bg;
        background: url("../images/mailing.png");
    }
    .portfolio-div {
        position: absolute;
        background: fade-out($accent-color, 0.1);
        top: -50%;
        left: 0;
        bottom: 0;
        right: 0;
        transition: all 0.5s;
        text-align: center;
        transform: rotateX(90deg);
        opacity: 0;
        transform-origin: center bottom;
        span {
            display: block;
            padding-top: 15%;
            font-weight: 300;
            color: white;
            text-transform: uppercase;
            font-size: 24px;
            line-height: 1.3;
        }
    }
}
.contact-section {
    text-align: center;
    .social-media {
        max-width: 700px;
        margin: 0 auto 100px;
        display: flex;
        justify-content: space-around;
        a {
            &:hover {
                transition: all 0.5s;
                transform: scale(1.2);
            }
        }
    }
    .bar {
        width: 250px;
        height: 0;
        border-top: 4px solid $accent-color;
        margin: 0 auto 70px;
    }
    .mail-big {
        display: block;
        font-size: 72px;
        font-weight: 300;
        text-decoration: none;
        color: $accent-color;
        line-height: 1;
        margin-bottom: 100px;
    }
}
.form-section {
    form {
        max-width: 450px;
        margin: 0 auto;
        input,
        textarea {
            border: 3px solid $accent-color;
            width: 100%;
            box-sizing: border-box;
            padding: 10px;
            &:focus {
                outline: none;
                border-color: $secondary-color;
            }
        }
        .submit-wrap {
            text-align: right;
        }
        [type="submit"] {
            background: $accent-color;
            color: white;
            font-size: 18px;
            text-transform: uppercase;
            width: 33%;
            margin-bottom: 100px;
            &:hover {
                background: $secondary-color;
            }
        }
        label {
            display: block;
            margin-bottom: 20px;
        }
    }
    .contact-info {
        label {
            width: 50%;
            float: left;
            box-sizing: border-box;
            &:first-of-type {
                padding-right: 10px;
            }
            &:last-of-type {
                padding-left: 10px;
            }
            @media screen and (max-width:500px) {
                width: 100%;
                float: none;
                padding: 0 !important;
            }
        }
    }
}
footer {
    background: url("../images/background.jpg");
    background-position: center bottom;
    background-size: cover;
    nav {
        text-align: center;
        padding: 30px 0;
        a {
            display: inline-block;
            margin-left: 20px;
            color: rgba(255, 255, 255, 0.7);
            @extend %small-bold;
            text-decoration: none;
            &:hover {
                color: rgba(255, 255, 255, 1);
            }
        }
    }
}
